.footer {
    margin: 0;
    padding: 0;
    min-width: 10px;
    margin-top: 68px;
    margin-bottom: 60px;
    font-family: "Inter", "Arial", sans-serif;
    font-style: normal;
    font-weight: 400;
    font-size: 18px;
    line-height: 22px;
    color: #545454;
  }

@media screen and (max-width: 700px) {
    .footer {
        margin-top: 48px;
        margin-bottom: 36px;
        margin-left: 19px;
        font-size: 14px;
        line-height: 17px;
        max-width: 282px;
      }
    }
